#searchScreen {
	ons-search-input > input.search-input {
	background-color: white;
	border-radius: 0px;
	height: 5vh;
	border-bottom: 1px solid #e0e0e0;
	line-height: 3vh;
	font-size: 2.2vh;
	padding: 0 0.5vh 0 6vh;
	background-image: url("data:image/svg+xml;base64,");
	background-size: 18px;
	background-position: 12px center;

}
.search_bg{
	width: 100% !important;
	height: 40vh !important;
}

div.navbar {
	position: fixed;
	top: 0px;
	background: black;
	div.profile-picture {
		width: 5vh;
		height: 5vh;
		margin-right: 10px;
		border-radius: 50%;
		border: 2px solid #fff;
		background: url('../img/profile-image.png');
		background-size: cover;
	}
	button.nav-notification {
		position: relative;
		div.notification {
			position: absolute;
			right: -5px;
			top: -2px;
		}
	}
}

div.container {
			padding: 0.6rem;
	}
	div.profile-pic-holder {
			position: relative;
	}

	.ons-card-profile {
		-webkit-font-smoothing: antialiased;
		box-shadow: 0 1px 2px rgba(0,0,0,0.12);
		border-radius: 0px;
		text-align: left;
		word-wrap: break-word;
		background: #FFF;
		position: relative;
		margin-top: 35rem;
		border-radius: 15px;
}

	div.profile-picture {
		width: 130px;
		height: 130px;
		margin: 0px auto;
		background-repeat: no-repeat;
		margin-bottom: 10px;
		border-radius: 58%;
		border: 2px solid #fff;
		position: relative;
		margin-top: -16rem !important;
}
.width-15 {
	width: 15rem !important;
}
.search-container button {
	float: right;
    padding: 6px 10px;
    margin-right: 19px;
    background: darkgray;
    font-size: 17px;
    border: none;
    cursor: pointer;
    position: fixed;
    right: -11px;
    margin-top: 0px;
    height: 2.6rem;
    border-radius: 0px 8px 8px 0px !important;
	}
.search-container button:hover {
	background: #ccc;
	}
.search-box {
	background: white;
    border-radius: 15px;
    font-size: large;
    padding-left: 1rem;
    width: 100%;
}
.search-header {
	font-size: medium;
	font-weight: bold;
	text-align: center
}
.social-media {
	display: flex;
	justify-content: center;
}
}